.cart-num {
	position: relative;
	bottom: 20px;
	left: 40px;
	background: hsl(26, 100%, 55%);
	padding: 0 10px 0 10px;
	text-align: center;
	color: #fff;
	align-self: center;
	border-radius: 10rem;
	font-weight: 1000;
}
.cart-img {
	height: 25px;
	cursor: pointer;
}

.cart-detail {
	position: absolute;
	background: #fff;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
	border-radius: 0.5rem;
	width: 400px;
	right: 200px;
	top: 120px;

	.cart__detail__title {
		padding: 1.3rem;
		border-bottom: 1px solid rgba(0, 0, 0, 0.1);
	}
	.cart__detail__items {
		padding: 0 1.5rem 1.5rem 1.5rem;
	}
	.cart-checkout {
		cursor: pointer;
		width: 100%;
		padding: 1rem;
		border: none;
		background: hsl(26, 100%, 55%);
		color: #fff;
		font-weight: 1000;
		border-radius: 0.5rem;
	}
}
