@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300&display=swap);
body {
	margin: 0;
	height: 100%;
}

html {
	height: 100vh;
	margin: 0 15vw 0 15vw;
	font-family: 'Kumbh Sans', sans-serif;
}

#root {
	height: 100%;
}

.navbar{display:flex;align-items:center;justify-items:center;grid-gap:50px;gap:50px;border-bottom:1px solid #d3d3d3;height:150px}.navbar a{display:flex;text-decoration:none;height:100%;align-items:center}.navbar a:hover{border-bottom:3px solid #ff7d1a}.navbar .cart{margin-left:auto;display:block}.navbar .profile-avatar{border-radius:50%;cursor:pointer}.navbar .profile-avatar:hover{outline:2px solid #ff7d1a}

.item{display:grid;grid-template-columns:auto auto auto;align-items:center;justify-items:center}.item-image{width:50px;height:50px;border-radius:5px}.item-delete{cursor:pointer}

.cart-num{position:relative;bottom:20px;left:40px;background:#ff7d1a;padding:0 10px 0 10px;text-align:center;color:#fff;align-self:center;border-radius:10rem;font-weight:1000}.cart-img{height:25px;cursor:pointer}.cart-detail{position:absolute;background:#fff;box-shadow:0px 0px 15px rgba(0,0,0,0.1);border-radius:0.5rem;width:400px;right:200px;top:120px}.cart-detail .cart__detail__title{padding:1.3rem;border-bottom:1px solid rgba(0,0,0,0.1)}.cart-detail .cart__detail__items{padding:0 1.5rem 1.5rem 1.5rem}.cart-detail .cart-checkout{cursor:pointer;width:100%;padding:1rem;border:none;background:#ff7d1a;color:#fff;font-weight:1000;border-radius:0.5rem}

.empty-cart{display:grid;place-content:center;padding:1.5rem;color:#8f8f8f}

.thumbnails{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:2rem;gap:2rem}.thumbnails img{grid-column:auto;cursor:pointer;max-width:100%;max-height:100%;border-radius:1rem}

.image-viewer{display:flex;flex-direction:column;grid-gap:2rem;gap:2rem;width:100%;height:100%}.image-viewer .product-image img{max-width:100%;max-height:100%;border-radius:1rem}

.counter-container{background:rgba(104,104,104,0.05);box-shadow:0px 0px 5px rgba(0,0,0,0.1);display:flex;align-items:center;border-radius:0.5rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.counter-container .minus,.counter-container .plus{font-size:2rem;border:none;cursor:pointer;padding:0 1rem 0 1rem;color:#ff7d1a;background:transparent}.counter-container span{font-weight:1000;padding:1rem;width:1rem;text-align:center}

.title{color:#ff7d1a}.product-description{display:flex;flex-direction:column;justify-content:center;grid-gap:20px;gap:20px}.product-description h1{margin:0}.product-description .real-price{line-height:2rem;font-weight:1000;text-decoration:line-through;color:gray}.product-price{display:flex;align-items:center;grid-gap:20px;gap:20px}.product-price span{color:#ff7d1a;font-weight:1000;background:#ffede0;padding:5px;border-radius:5px}.description-buttons{display:flex;justify-content:space-between;grid-gap:20px;gap:20px}.description-buttons .addtocart-button{box-shadow:5px 2px 10px #ff7d1a;border:none;border-radius:0.5rem;width:100%;color:#fff;background:#ff7d1a;cursor:pointer;display:flex;align-items:center;justify-content:center}.description-buttons .addtocart-button .cart-img{-webkit-transform:scale(0.9);transform:scale(0.9);align-self:center}

.product-page{display:grid;grid-gap:5vw;gap:5vw;grid-template-columns:repeat(2, 50%);padding:7vw}

